body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 11px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --height: 3rem;
  --cell-width: 4.5rem;
  --cell-width-header1: 3rem;
  --max-table-width: calc(
    var(--cell-width) * 14 + var(--cell-width-header1)
  );
}

table {
  border-collapse: collapse;
  border-spacing: 0px;
}
table,
th,
td {
  height: 3rem;
  height: var(--height);
  border: 1px solid black;
}

.mainDiv {
  width: 100%;
}
@media screen and (min-width: 64rem) {
  .mainDiv {
    display: flex;
    justify-content: center;
    align-self: center;
    overflow-x: scroll;
  }
}

.main {
  height: 100%;
  width: 100%;
}

@media screen and (min-height: 45rem) {
  .main {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-self: center;
    overflow-y: scroll;
  }
}

.rowPeriods,
.header4,
.header {
  font-size: 1.3rem;
  font-weight: 400;
}

.titleClass {
  font-size: 1.3rem;
  font-weight: 500;
}

.header {
  width: 4.5rem;
  width: var(--cell-width);
}

.header4 {
  width: calc(4.5rem * 4);
  width: calc(var(--cell-width) * 4);
}

.header0 {
  width: 4.5rem;
  width: var(--cell-width);
}
.header1 {
  width: 3rem;
  width: var(--cell-width-header1);
}

.cellWidth {
  width: 4.5rem;
  width: var(--cell-width);
  padding: 3px;
}
.cellWidth3 {
  width: calc(4.5rem * 3);
  width: calc(var(--cell-width) * 3);
}
.cellWidth4 {
  width: calc(4.5rem * 4);
  width: calc(var(--cell-width) * 4);
}

.rowPeriods,
.header0,
.header,
.titleClass,
.infoMessage {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.bColorRed {
  background-color: #ee7637;
}

.titleColor,
.bColorYellow {
  background-color: #fedcae;
}

.bColorGreen {
  background-color: #3dadc1;
}

.bColorNoColor,
.bColorRed,
.bColorYellow,
.bColorGreen {
  cursor: pointer;
  height: 100%;
}

.cellKeyAndNum {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cellFlexStart {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.cellFlexEnd {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.cellFlexStart,
.cellFlexEnd,
.cellNum {
  font-size: 0.6rem;
}

.cellKey {
  font-size: 1rem;
  font-weight: 700;
}

.relativePosition {
  position: relative;
  height: 100%;
  width: 100%;
}

.cellShadow {
  position: absolute;
  top: -5px;
  left: -5px;
  box-shadow: black 0px 13px 13px 5px;
}

.infoMessage {
  padding: 8px 0 8px 2px;
  background-color: #fedcae;
  max-width: calc(
    4.5rem * 14 + 3rem);
  max-width: var(--max-table-width);
  width: 100%;
}

.popup__full_screen_div {
  background-color: transparent;
  align-items: center;
  bottom: 0;
  display: flex;
  justify-content: center;
  left: 0;
  overflow: hidden;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 220;
}

.popup__full_screen_div_opacity {
  background-color: black;
  bottom: 0;
  left: 0;
  opacity: 0.5;
  overflow: hidden;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 210;
}

.popup__window {
  background-color: white;
  border-radius: 5px;
  padding: 10px 25px 10px 20px;
  position: relative;
  width: calc(var(--max-table-width) - 200px);
  box-shadow: black 0px 13px 26px 0px;
  font-size: 0.9rem;
  text-align: justify;
  text-justify: inter-word;
  text-indent: 40px;
  line-height: 1.2rem;
  width: 90%;
}

@media screen and (min-width: 64rem) {
  .popup__window {
    width: calc(var(--max-table-width) - 200px) !important;
  }
}

.popup__list {
  max-height: 500px;
  overflow-y: scroll;
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 10px;
}

.popup__cancel {
  position: absolute;
  top: 2px;
  right: 10px;
  cursor: pointer;
}

.popup__title {
  font-size: 20px;
  font-weight: 500;
  padding: 8px 0 8px 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.modalButtonContainer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.modalButton {
  padding: 10px;
  background-color: #3dadc1;
  color: white;
  border-radius: 5px;
  box-shadow: black 0px 13px 26px 0px;
}

.modalElementsDiv {
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalElements {
  height: calc(var(--height) * 2);
  width: calc(var(--width) * 3);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

